    <div class="header">
      <img src="@/assets/ms logo.png" alt="Headphone Image" class="headphone-image" />
      <div class="product-info">
        <h2>Mask Sunshine</h2>
        <p>LED Device</p>
      </div>
    </div>

    <!-- Tab Menu -->
    <div class="tabs">
      <button @click="selectTab('status')" :class="{ active: selectedTab === 'status' }">Massage Intensity</button>
      <button @click="selectTab('sound')" :class="{ active: selectedTab === 'sound' }">LED Intensity</button>
      <button @click="selectTab('system')" :class="{ active: selectedTab === 'system' }">User Settings</button>
      <button @click="selectTab('service')" :class="{ active: selectedTab === 'service' }">Product Description</button>
    </div>

    <!-- Massage Intensity Tab Content -->
    <div v-if="selectedTab === 'status'" class="tab-content">
      <h3>Massage Intensity Control</h3>
      <div class="slider-container">
        <label for="massage-intensity">Adjust Intensity:</label>
        <input type="range" id="massage-intensity" v-model="massageIntensity" min="0" max="100" />
        <span>{{ massageIntensity }}%</span>
      </div>
      <div class="controls">
        <button class="primary-button" @click="toggleMassagePower" :class="{ active: massagePower }" :style="{ backgroundColor: massagePower ? '#000' : '#2E968F' }">
          {{ massagePower ? 'Turn Off' : 'Turn On' }}
        </button>
      </div>
    </div>

    <!-- LED Intensity Tab Content -->
    <div v-if="selectedTab === 'sound'" class="tab-content">
      <h3>LED Intensity Control</h3>
      <div class="slider-container">
        <label for="volume">Adjust Brightness:</label>
        <input type="range" id="volume" v-model="volume" min="0" max="100" />
        <span>{{ volume }}%</span>
      </div>
      <div class="controls">
        <button class="primary-button" @click="toggleMassagePower" :class="{ active: massagePower }" :style="{ backgroundColor: massagePower ? '#000' : '#2E968F' }">
          {{ massagePower ? 'Turn Off' : 'Turn On' }}
        </button>
      </div>
    </div>

    <!-- User Settings Tab Content -->
    <div v-if="selectedTab === 'system'" class="tab-content">
      <h3>User Settings</h3>
      <div class="input-group">
        <label for="custom-name">Setting Name:</label>
        <input type="text" id="custom-name" v-model="customName" placeholder="Enter name" class="input-field" />
      </div>
      <div class="slider-container unified-sliders">
        <label for="custom-massage">Massage Intensity:</label>
        <input type="range" id="custom-massage" v-model="customMassage" min="0" max="100" />
        <span>{{ customMassage }}%</span>
      </div>
      <div class="slider-container unified-sliders">
        <label for="custom-led">LED Intensity:</label>
        <input type="range" id="custom-led" v-model="customLed" min="0" max="100" />
        <span>{{ customLed }}%</span>
      </div>

      <!-- Morning/Night Preset Buttons -->
      <div class="preset-buttons">
        <button class="preset-button" @click="applyMorningPreset">Morning Mode</button>
        <button class="preset-button" @click="applyNightPreset">Night Mode</button>
      </div>

      <button class="primary-button" @click="saveCustomSetting">Save</button>

      <div v-if="savedSettings.length">
        <h4>Saved Settings</h4>
        <ul>
          <li v-for="(setting, index) in savedSettings" :key="index">
            {{ setting.name }}: Massage Intensity {{ setting.massage }}%, LED Intensity {{ setting.led }}%
          </li>
        </ul>
      </div>
    </div>

    <!-- Product Description Tab Content -->
    <div v-if="selectedTab === 'service'" class="tab-content">
      <h3>Product Description</h3>
      <div class="product-image-container">
        <img src="@/assets/product.jpeg" alt="Product Image" class="product-image" />
      </div>
      <p class="product-description">
        Mask Sunshine is a cutting-edge LED device that allows you to freely adjust the massage intensity and LED intensity.
        It provides optimized settings for both morning and night to help with skin care and provide a comfortable user experience.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedTab: 'status', // Default selected tab is 'Massage Intensity'
      massageIntensity: 50, // Default massage intensity value
      massagePower: false, // Massage power state

      volume: 50, // Default LED intensity value
      ambientSound: false, // LED brightness control mode
      offMode: false, // LED power state

      customName: '', // User-defined setting name
      customMassage: 50, // User-defined massage intensity
      customLed: 50, // User-defined LED intensity
      savedSettings: [] // List of saved user settings
    };
  },
  methods: {
    selectTab(tab) {
      this.selectedTab = tab;
    },
    toggleMassagePower() {
      this.massagePower = !this.massagePower;
    },
    toggleAmbientSound() {
      this.ambientSound = true;
      this.offMode = false;
    },
    toggleOff() {
      this.ambientSound = false;
      this.offMode = true;
    },
    saveCustomSetting() {
      if (this.customName) {
        const newSetting = {
          name: this.customName,
          massage: this.customMassage,
          led: this.customLed
        };
        this.savedSettings.push(newSetting);
        this.customName = ''; // Clear input field after saving
      } else {
        alert('Please enter a setting name.');
      }
    },
    applyMorningPreset() {
      this.customMassage = 24; // Morning preset: Massage intensity 24
      this.customLed = 57; // Morning preset: LED intensity 57
    },
    applyNightPreset() {
      this.customMassage = 88; // Night preset: Massage intensity 88
      this.customLed = 15; // Night preset: LED intensity 15
    }
  }
};
</script>

